html, body {
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
}

.bg-blue {
    background-color: #b5e0fb;
}

.parent {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    background-color: #27377f;
    padding: 2vw;
}

.item {
    /*width: 100% !important;*/
    /*min-height: 100% !important;*/
    height: 14vw;
    width: 14vw;
    cursor: pointer;
    user-select: none;
    background-color: #f5abcd;
    z-index: 1;
    font-size: 1.8vmin;
    font-weight: bolder;
}

.item.empty {
    visibility: hidden;
}

.item.isReady {
    background-color: #b5e0fb;
    font-weight: bold;
}